import { useContext, useState } from "react";
import { client } from "../../services/client";
import { AuthContext } from "../../contexts/AuthContext";
import { useNavigate } from "react-router-dom";
import Cookies from "js-cookie";
import { authHeaders } from "../../services/authService";

export const useGuestUserLogin = () => {
  const { setIsLoggedIn, setCurrentUser, setFlashMessage } = useContext(AuthContext);
  const navigate = useNavigate();
  const [generalErrors, setGeneralErrors] = useState("");

  const handleGuestLogin = async () => {
    try {
      const res = await client.post(`/auth/guest_login`, {});
      if (res.status === 200) {
        Cookies.set("_access_token", res.headers["access-token"]);
        Cookies.set("_client", res.headers["client"]);
        Cookies.set("_uid", res.headers["uid"]);

        setCurrentUser(res.data.user);
        setIsLoggedIn(true);

        navigate("/");

        setFlashMessage("");
        setTimeout(() => {
          setFlashMessage("");
          // window.location.reload();
        }, 2000);
        console.log("");

        setGeneralErrors("");
      }
    } catch (e) {
      console.error("", e);
      setGeneralErrors("");
      setFlashMessage("");
    }
  };

  // 
  const handleGuestLogout = async () => {
    try {
      const res = await client.delete("/auth/sign_out", { headers: authHeaders() });

      if (res.status === 200) {
        // 
        Cookies.remove("_access_token");
        Cookies.remove("_client");
        Cookies.remove("_uid");

        setIsLoggedIn(false);
        setCurrentUser(undefined);

        console.log("");
        navigate("/signin");
        window.location.reload();
      } else {
        throw new Error("");
      }
    } catch (e) {
      console.error(e);
      setFlashMessage("");
    }
  };

  return {
    handleGuestLogin,
    handleGuestLogout,
    generalErrors,
  };
};
